:root {
  --accent: #007bff;
  --text-light: #fff;
  --text-dark: #333;
  --bg-fallback: #1a1a1a;
}

body { 
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
  margin: 0; 
  line-height: 1.6; 
  color: var(--text-dark); 
  scroll-behavior: smooth;
}

/* --- NAVIGATION --- */
nav { 
  position: fixed; /* Keeps it at the top while scrolling */
  top: 0; 
  width: 100%; 
  padding: 25px 0;
  text-align: center; 
  z-index: 1000;
  background-color: transparent; /* Start transparent */
  transition: all 0.4s ease-in-out;
}

/* Applied via JavaScript in App.js when scrolling */
nav.scrolled {
  background-color: rgba(0, 0, 0, 0.75); /* Darker background */
  backdrop-filter: blur(10px);           /* Modern glass effect */
  padding: 15px 0;                       /* Shrink slightly */
  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
}

nav a { 
  color: white; 
  text-decoration: none; 
  margin: 0 15px; 
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 1px;
}

nav a:hover {
  color: var(--accent);
}

/* --- HERO SECTION --- */
header { 
  background-color: var(--bg-fallback); 
  background-size: cover;
  background-position: center;
  background-attachment: scroll; 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--text-light);
  transition: background-image 0.8s ease-in-out;
  padding: 0 20px;
}

header h1 { 
  font-size: clamp(2.2rem, 8vw, 4rem); 
  margin: 0; 
  background: none;
  border: none;
  padding: 0;
  text-shadow: 2px 2px 20px rgba(0,0,0,0.8); 
}

.profile-circle {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  border: 4px solid var(--text-light);
  object-fit: cover;
  margin: 20px 0;
  box-shadow: 0 8px 25px rgba(0,0,0,0.5);
}

header p { 
  font-size: 1.4rem; 
  font-weight: 400;
  margin-top: 10px;
  text-shadow: 1px 1px 10px rgba(0,0,0,0.8);
}

/* --- BUTTONS --- */
.button-group {
  display: flex;
  gap: 15px;
  margin-top: 35px;
}

.btn {
  display: inline-block;
  padding: 12px 30px;
  border-radius: 50px;
  font-weight: bold;
  text-decoration: none;
  transition: all 0.3s ease;
}

.btn-primary { background: var(--accent); color: white; }
.btn-primary:hover { background: #0056b3; transform: scale(1.05); }

.btn-outline {
  background: transparent;
  color: white;
  border: 2px solid white;
}
.btn-outline:hover { background: white; color: var(--text-dark); }

.linkedin-btn { background-color: #0077b5; color: white; }

/* --- CONTENT SECTIONS --- */
section { padding: 100px 20px; max-width: 1000px; margin: auto; }

.grid { 
  display: grid; 
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); 
  gap: 25px; 
}

.card { 
  border: 1px solid #eee; 
  padding: 30px; 
  border-radius: 15px; 
  box-shadow: 0 5px 15px rgba(0,0,0,0.05);
  background: white;
  color: var(--text-dark);
}

.accent-link { color: var(--accent); text-decoration: none; font-weight: bold; }

footer { text-align: center; padding: 50px; background: #111; color: #777; }

/* --- PHONE OPTIMIZATIONS --- */
@media (max-width: 600px) {
  nav {
    padding: 10px 0;
    background-color: rgba(0, 0, 0, 0.9); /* More solid on small screens */
  }
  
  nav a {
    display: inline-block;
    margin: 5px 8px;
    font-size: 0.75rem;
  }

  header h1 { font-size: 2.2rem; }
  .profile-circle { width: 140px; height: 140px; }
  .button-group { flex-direction: column; width: 100%; align-items: center; }
  .btn { width: 85%; text-align: center; }
}